import React, {useState, useEffect} from 'react';
import './App.css';
import potter from './img/potter.png';
import backgroundSky from './img/backgroundSky.jpg';
import backgroundTowers from './img/backgroundTowers.png';
import cloud from './img/cloud.png';
import firebase from './firebase';
import roblox from './snd/roblox.mp3';
import zelda from './snd/zelda.mp3';
import success from './snd/success.mp3';
import jump from './snd/jump.mp3';

// const CANVAS_HEIGHT = 750;
// const CANVAS_WIDTH = 1500;
const CANVAS_HEIGHT = 750;
const CANVAS_WIDTH = 1500;
// const GRAVITY = 0.125;
const GRAVITY = 0.2; 
const OBSTACLE_WIDTH = 10;
const playerX = 200; //0.2*CANVAS_WIDTH;

let SPEED = 3;
let ANSWER = 1;  
let SCORE = 0;
let SHOW_RESULTS = false;
let PLAYER_HEIGHT = 0;

let audio = new Audio();
audio.src = roblox;

let audioSuccess = new Audio();
audioSuccess.src = success;

let audioJump = new Audio();
audioJump.src = jump;

let backgroundMusic = new Audio();
backgroundMusic.src = zelda;
backgroundMusic.loop = true;

function randomNum(a, b) {
    return Math.floor(Math.random() * b) + a; 
}

class Game {
    constructor() {
        this.gameState = 0;
    }    
}

class Player {
    constructor() {
        this.width = 70;
        this.height = 70;
        this.position = {
            x: playerX,
            y: 0.5*CANVAS_HEIGHT
        }
        this.transform = {
            x: playerX,
            y: 0.5*CANVAS_HEIGHT
        }
        this.gravityDeceleration = 0;
        this.jumpBoost = 0;
        this.stop = 0;
        this.rotation = 0;
        this.direction = 1;
        this.x1 = 0;
        this.y1 = 0;
        this.h1 = 0;
        this.k1 = 0;
        this.fallen = 0;
    }

    draw(ctx) {
        ctx.save();
        
        let upTilt = -50;
        let downTilt = 80;

        this.rotation += this.direction;
        this.direction += 0.5;
        if (this.direction > 1) {
            this.direction = 1;
        }

        if (this.rotation > downTilt) {
            this.rotation = downTilt;
        }
        if (this.rotation < upTilt) {
            this.rotation = upTilt;
        }

        var sin = Math.sin(this.rotation * Math.PI / 180);
        var cos = Math.cos(this.rotation * Math.PI / 180);

        let buffer = 40;
        this.y1 = buffer;
        this.h1 = this.height*Math.cos(this.rotation * Math.PI/180) + this.width*Math.sin(this.rotation * Math.PI/180) - buffer;
        this.x1 = this.height*Math.cos((90 - this.rotation) * Math.PI/180) - buffer;
        this.k1 = this.width*Math.cos(this.rotation * Math.PI/180) + this.height*Math.sin(this.rotation * Math.PI/180) - buffer;
        if (this.rotation < 0) {
            this.y1 = this.width*Math.cos((90 - this.rotation) * Math.PI/180) + buffer;
            this.h1 = this.height*Math.cos(this.rotation * Math.PI/180) - this.width*Math.sin(this.rotation * Math.PI/180) - 1.5*buffer;
            this.x1 = -buffer;
            this.k1 = this.width*Math.cos(this.rotation * Math.PI/180) - this.height*Math.sin(this.rotation * Math.PI/180) - 1.5*buffer;
        }
        this.position.x = this.transform.x - this.x1 - 50;
        this.position.y = this.transform.y + this.y1;
        // ctx.fillRect(this.position.x, this.position.y, this.k1, this.h1); // hit detection

        ctx.transform(cos, sin, -sin, cos, this.transform.x - this.width*cos/2, this.transform.y);

        let imgSrc = document.getElementById("spriteID");
        ctx.drawImage(imgSrc, 0, 0, this.width, this.height);
        ctx.restore();
    }

    fall() {
      this.transform.y += 20;
      if (this.transform.y > CANVAS_HEIGHT) {
        this.fallen = 1;
      }
    }

    update(deltaTime) {
        this.gravityDeceleration += GRAVITY;
        this.transform.y += this.gravityDeceleration; // toggle gravity
        PLAYER_HEIGHT = this.position.y;
    }

    jump() {
        this.gravityDeceleration = -6;
        this.direction = -7;
        audioJump.play();
    }

}

class Obstacle {
    constructor() {
        this.tops = [0, 10, 20, 30, 40];
        this.heights = [10, 10, 10, 10, 10];
        this.speed = 10;
        this.positionX = CANVAS_WIDTH *.8;        
        this.window = 150;
        this.correct = 1;
    }

    create() {
        this.tops[0] = 0;
        this.tops[1] = randomNum(CANVAS_HEIGHT*0.05, CANVAS_HEIGHT*0.2);
        this.tops[2] = this.tops[1] + this.window;
        this.tops[3] = this.tops[2] + randomNum(CANVAS_HEIGHT*0.1, CANVAS_HEIGHT*0.15);
        this.tops[4] = this.tops[3] + this.window;
        this.correct = 1 + 2*(randomNum(1, 2)-1); // 1 or 3
        
        this.answerCorrect = ANSWER * randomNum(2, 9);
        this.answerIncorrect = ANSWER * randomNum(2, 9) + randomNum(1, ANSWER-1);
    }

    draw(ctx) {
        for (let i = 0; i < this.tops.length; i++) {
            // ctx.fillStyle = "rgb(100, 100, 100)";
            ctx.fillStyle = "#b58b00";
            if (i !== 1 && i !== 3) {
                if (i+1 < this.tops.length) {
                    ctx.fillStyle = "#000";
                    ctx.fillRect(this.positionX-1, this.tops[i], OBSTACLE_WIDTH+2, this.tops[i+1] - this.tops[i]);
                    ctx.fillStyle = "#b58b00";
                    ctx.fillRect(this.positionX, this.tops[i], OBSTACLE_WIDTH, this.tops[i+1] - this.tops[i]);
                } else {
                    ctx.fillStyle = "#000";
                    ctx.fillRect(this.positionX-1, this.tops[i], OBSTACLE_WIDTH+2, CANVAS_HEIGHT - this.tops[i]);
                    ctx.fillStyle = "#b58b00";
                    ctx.fillRect(this.positionX, this.tops[i], OBSTACLE_WIDTH, CANVAS_HEIGHT - this.tops[i]);
                }
            } else {
                ctx.beginPath();
                ctx.lineWidth = 14;
                ctx.strokeStyle = "#000";
                // ctx.ellipse(this.positionX+10, this.tops[i] + (this.tops[i+1] - this.tops[i])/2, 40, (this.tops[i+1] - this.tops[i])/2, 0, 0, 2 * Math.PI); // 3d outline (behind)
                ctx.ellipse(this.positionX, this.tops[i] + (this.tops[i+1] - this.tops[i])/2, 40, (this.tops[i+1] - this.tops[i])/2, 0, 0, 2 * Math.PI); // outline
                ctx.stroke();
                ctx.beginPath();
                ctx.lineWidth = 10;
                ctx.strokeStyle = "#b58b00";
                ctx.ellipse(this.positionX, this.tops[i] + (this.tops[i+1] - this.tops[i])/2, 40, (this.tops[i+1] - this.tops[i])/2, 0, 0, 2 * Math.PI);
                ctx.stroke();
                ctx.fillStyle = "rgba(255, 255, 255, 0)";
            }

            // draw scores
            if (i === 1 || i === 3) { 
                ctx.fillStyle = "blue";
                ctx.font = "30px Verdana";
                if (i === this.correct) {
                    ctx.fillText(this.answerCorrect, this.positionX - 20, this.tops[i] + this.window/2);
                } else {
                    ctx.fillText(this.answerIncorrect, this.positionX - 20, this.tops[i] + this.window/2);
                }
            }
        }
    }

    update(deltaTime) {
        this.positionX -= SPEED;
        if (this.positionX + OBSTACLE_WIDTH < 0) {
            this.positionX = CANVAS_WIDTH;
            this.create();
        }
        SCORE += SPEED;
        document.getElementById("scoreID").innerHTML = "Score: " + SCORE;
    }
}

class Bludger {
  constructor() {
    this.radius = 30;
    this.position = {
      x: CANVAS_WIDTH + this.radius,
      y: CANVAS_HEIGHT/2
    }   
    this.bludger_hit = 0;
  }
  
  draw(ctx) {
    ctx.fillStyle = "rgb(0, 0, 0)";
    ctx.strokeStyle = "rgb(0, 0, 0)";
    if (this.position.x > playerX) {
      ctx.beginPath();
      ctx.arc(this.position.x, this.position.y, this.radius, 0, 2 * Math.PI);
      ctx.fill();
      ctx.stroke();
      // ctx.fillRect(this.position.x, this.position.y, 10, 10);
    } else {
      this.bludger_hit = 1;
      // let audio = new Audio("./snd/roblox.mp3");
      console.log("got em");
    }
  }

  update(deltaTime) {
    if (SHOW_RESULTS) {
      let bludgerSpeed = 50;
      this.position.x -= bludgerSpeed;
      let w = (CANVAS_WIDTH - this.position.x)/(CANVAS_WIDTH - playerX);
      // this.position.y = (this.position.y - PLAYER_HEIGHT) * (1 - playerX)/(CANVAS_WIDTH - playerX);      
      this.position.y = CANVAS_HEIGHT/2 - w*(CANVAS_HEIGHT/2 - PLAYER_HEIGHT)
    }    
  }
  
}

class Snitch {
  constructor() {
    this.position = {
      x: playerX + 200,
      y: PLAYER_HEIGHT
    }
    this.radius = 5;
  }

  draw(ctx) {
      ctx.strokeStyle = "rgb(0, 0, 0)";
      ctx.fillStyle = "#ffcc00";
      ctx.lineWidth = 1;
      ctx.beginPath();
      ctx.arc(this.position.x, this.position.y, this.radius, 0, 2 * Math.PI);
      ctx.fill();
      ctx.stroke();
  }

  update(deltatime) {
    // let snitchSpeed = 3;
    // let snitchMovement = snitchSpeed * (1 - 2*(randomNum(1, 2) - 1));
    // let bounds = 0;
    // if (this.position.y + Math.abs(snitchSpeed) > PLAYER_HEIGHT + bounds) {
    //     snitchMovement = -Math.abs(snitchMovement);
    // }
    // if (this.position.y - Math.abs(snitchSpeed) < PLAYER_HEIGHT - bounds) {
    //     snitchMovement = Math.abs(snitchMovement);
    // }

    // this.position.y += snitchMovement;

    this.position.y = PLAYER_HEIGHT;
    this.position.x = playerX + 200;
  }
}

class Cloud {
    constructor() {
        this.position = {
            x: CANVAS_WIDTH,
            y: randomNum(10, CANVAS_HEIGHT/2)
        }
        this.size = randomNum(20, 40);
        this.cloudSpeed = randomNum(1, 3);
    }

    draw(ctx) {
        let cloudSrc = document.getElementById("cloudID");
        ctx.drawImage(cloudSrc, this.position.x, this.position.y, this.size, this.size);
    }

    update(deltaTime) {
        this.position.x -= this.cloudSpeed;
        if (this.position.x < -this.size) {
            this.position.x = CANVAS_WIDTH;
            this.size = randomNum(20, 40);
            this.cloudSpeed = randomNum(1, 3);
            this.position.y = randomNum(10, CANVAS_HEIGHT/2);
        }
    }


}

const Flappy = () => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");

    useEffect(() => {
        let savedUsername = sessionStorage.getItem("savedUsername");
        if (savedUsername) {
            showUserDetails(savedUsername)
        } else {
            document.getElementsByClassName("signIn")[0].style.display = "none";
            document.getElementsByClassName("signUp")[0].style.display = "block";
        }

        
    });

    async function showUserDetails(savedUsername) {
        document.getElementsByClassName("signIn")[0].style.display = "block";
        document.getElementsByClassName("signUp")[0].style.display = "none";
        document.getElementById("usernameID").innerHTML = "Welcome " + savedUsername;   
        let points = await firebase.getPoints(savedUsername);
        document.getElementById("pointsID").innerHTML = "You have " + points + " points";
    }
    
    async function collisionCheck(game, player, allObstacles, bludger, ctx) {
        if (player.transform.y > CANVAS_HEIGHT) {
            player.transform.y = CANVAS_HEIGHT;
            game.gameState = -1;
        }

        let passed = true;
        if (player.position.x + player.k1 > allObstacles.positionX
            && player.position.x < allObstacles.positionX + OBSTACLE_WIDTH) {
                passed = false;
                if (player.position.y > allObstacles.tops[allObstacles.correct]
                    && player.position.y + player.h1 < allObstacles.tops[allObstacles.correct] + allObstacles.window) {
                        passed = true;
                        audioSuccess.play();
                }
        }

        if (passed === false) {
            game.gameState = -1;
            SHOW_RESULTS = true;
            // document.getElementById("testID").innerHTML = "HIT";
            allObstacles.draw(ctx);
            player.draw(ctx);
            audio.play();
            // PLAYER_HEIGHT = player.position.y;
            // this.bludger_hit = 0;
            // bludger.position.x = CANVAS_WIDTH;

            // bludger.hit(ctx);
            await updatePoints();            
        } else {
            // document.getElementById("testID").innerHTML = "MISS";
        }
        
    }

    async function updatePoints() {
        let savedUsername = sessionStorage.getItem("savedUsername");
        if (savedUsername) {
            await firebase.updatePoints(savedUsername, SCORE);
        }
    }

    function weatherCheck() {
        // let weather = getWeather();
        let weather = 1;
        let backgroundImg = "sunnyBackground";
        if (weather === "rain") {
            backgroundImg = "rainBackground";
        } else if (weather === "overcast") {
            backgroundImg = "overcastBackground";
        }
        return backgroundImg;
    }
    
    
    async function startGame() {
        console.log("game has begun, speed is " + SPEED);
        
        if (backgroundMusic.paused) {
            backgroundMusic.play();
        } else {
            backgroundMusic.play();
        }

        weatherCheck();
        
        document.getElementById("gameScreen").width = CANVAS_WIDTH;
        document.getElementById("gameScreen").height = CANVAS_HEIGHT;
        document.getElementById("speedID").innerHTML = "Speed: " + SPEED;
        // let canvas = document.getElementById("gameScreen");
        // let ctx = canvas.getContext("2d");
        
        let game = new Game();
        game.gameState = 1;

        let btns = document.getElementsByClassName("speedBtn");
        for (let i = 0; i < btns.length; i++) {
            btns[i].disabled = true;
        }

        let player = new Player();
        // let player = new TestPlayer();

        let snitch = new Snitch();

        document.addEventListener("keydown", event => {
            switch (event.key) {
                case " ":
                    //player.jumpBoost = 1;
                    player.jump();
                    break;
                default:
            }
        });

        let canvas = document.getElementById("gameScreen");
        canvas.addEventListener("click", function() { player.jump() });
        let ctx = canvas.getContext("2d");
        ctx.font = "60px harryPotterFont";
        
        
        player.draw(ctx);

        // countdown delay
        for (let i = 4; i > 0; i--) {
            ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
            /// font doesnt load straight away
            if (i === 4) {
                ctx.fillStyle = "#fff";
                ctx.fillText("Starting...", -CANVAS_WIDTH, 0);
                await new Promise(resolve => {
                    setTimeout(() => {
                    resolve(2);
                    }, 100);
                });
            } else {
                // ctx.font = "60px Verdana";
                ctx.fillStyle = "rgb(100, 100, 100)";
                ctx.fillText("Multiples of " + ANSWER, CANVAS_WIDTH*0.3, CANVAS_HEIGHT/2);
                ctx.fillText(i, CANVAS_WIDTH*0.4, CANVAS_HEIGHT/2+100);
                // delay countdown
                await new Promise(resolve => {
                    setTimeout(() => {
                    resolve(2);
                    }, 1000);
                });
            }
            
        }
        
        let allObstacles = new Obstacle();
        allObstacles.create();

        let bludger = new Bludger();

        let clouds = [];

        requestAnimationFrame(
            function(timestamp) {
                gameLoop(timestamp, ctx, game, player, allObstacles, bludger, snitch, clouds);
            }
        );
    }

    function makeQuestion() {
        ANSWER = randomNum(2, 10);
    }

    let lastTime = 0;
    function gameLoop(timestamp, ctx, game, player, allObstacles, bludger, snitch, clouds) {
        let deltatime = timestamp - lastTime;
        lastTime = timestamp;
        let backgroundSkySrc = document.getElementById("backgroundSkyID");
        let backgroundTowersSrc = document.getElementById("backgroundTowersID");
        let imgHeight = CANVAS_HEIGHT/5;
        let ratio = imgHeight / backgroundTowersSrc.height;
        backgroundTowersSrc.height *= ratio;
        backgroundTowersSrc.width *= ratio;
        let slide = SCORE*SPEED/100;
        slide = SCORE/15; // constant speed


        if (game.gameState === 1) {
            ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
            ctx.drawImage(backgroundSkySrc, 0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);

            if (randomNum(1, 100) === 1 && clouds.length < 5) {
                let cloud = new Cloud();
                clouds.push(cloud);
            }
            for (let c = 0; c < clouds.length; c++) {
                clouds[c].update(deltatime);
                clouds[c].draw(ctx);
            }

            let i = 0;
            while ((i-1)*backgroundTowersSrc.width < CANVAS_WIDTH) {
                let xpos = -slide%backgroundTowersSrc.width + i*backgroundTowersSrc.width;
                ctx.drawImage(backgroundTowersSrc, xpos, CANVAS_HEIGHT-backgroundTowersSrc.height, backgroundTowersSrc.width, backgroundTowersSrc.height);
                i++;
            }

            ctx.font = "60px harryPotterFont";
            ctx.fillStyle = "rgb(100, 100, 100)";
            ctx.fillText("Multiples of " + ANSWER, CANVAS_WIDTH*0.3, CANVAS_HEIGHT/2);
            allObstacles.update(deltatime);
            allObstacles.draw(ctx);

            player.update(deltatime);
            player.draw(ctx);

            snitch.update(deltatime);
            snitch.draw(ctx);

            // bludger.draw(ctx);

            requestAnimationFrame(
                function(timestamp) {
                    gameLoop(timestamp, ctx, game, player, allObstacles, bludger, snitch, clouds);
                }
            );

            collisionCheck(game, player, allObstacles, bludger, ctx);

        } else {
            if (bludger.bludger_hit === 0) {
                ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
                ctx.drawImage(backgroundSkySrc, 0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
         
                if (randomNum(1, 100) === 1 && clouds.length < 5) {
                    let cloud = new Cloud();
                    clouds.push(cloud);
                }
                for (let c = 0; c < clouds.length; c++) {
                    clouds[c].update(deltatime);
                    clouds[c].draw(ctx);
                }

                let i = 0;
                while ((i-1)*backgroundTowersSrc.width < CANVAS_WIDTH) {
                    let xpos = -slide%backgroundTowersSrc.width + i*backgroundTowersSrc.width;
                    ctx.drawImage(backgroundTowersSrc, xpos, CANVAS_HEIGHT-backgroundTowersSrc.height, backgroundTowersSrc.width, backgroundTowersSrc.height);
                    i++;
                }

                ctx.font = "60px harryPotterFont";
                ctx.fillStyle = "rgb(100, 100, 100)";
                ctx.fillText("Multiples of " + ANSWER, CANVAS_WIDTH*0.3, CANVAS_HEIGHT/2);
                allObstacles.draw(ctx);
                player.draw(ctx);
    
                bludger.update(deltatime);
                bludger.draw(ctx);
                requestAnimationFrame(
                  function(timestamp) {
                    gameLoop(timestamp, ctx, game, player, allObstacles, bludger, snitch, clouds);

                  }
                );
            } else if (player.fallen === 0) {
                ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
                ctx.drawImage(backgroundSkySrc, 0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
                    
                if (randomNum(1, 100) === 1 && clouds.length < 5) {
                    let cloud = new Cloud();
                    clouds.push(cloud);
                }
                for (let c = 0; c < clouds.length; c++) {
                    clouds[c].update(deltatime);
                    clouds[c].draw(ctx);
                }
                
                let i = 0;
                while ((i-1)*backgroundTowersSrc.width < CANVAS_WIDTH) {
                    let xpos = -slide%backgroundTowersSrc.width + i*backgroundTowersSrc.width;
                    ctx.drawImage(backgroundTowersSrc, xpos, CANVAS_HEIGHT-backgroundTowersSrc.height, backgroundTowersSrc.width, backgroundTowersSrc.height);
                    i++;
                }
              ctx.font = "60px harryPotterFont";
              ctx.fillStyle = "rgb(100, 100, 100)";
              ctx.fillText("Multiples of " + ANSWER, CANVAS_WIDTH*0.3, CANVAS_HEIGHT/2);
              allObstacles.draw(ctx);
              player.fall();              
              player.draw(ctx);
              requestAnimationFrame(
                function(timestamp) {
                    gameLoop(timestamp, ctx, game, player, allObstacles, bludger, snitch, clouds);
                }
              );
            }
            console.log("game over");
            document.getElementById("resetBtn").style.display = "block";
            let btns = document.getElementsByClassName("speedBtn");
            for (let i = 0; i < btns.length; i++) {
                btns[i].disabled = false;
            }
        }
    }

    async function reset() {
        let savedUsername = sessionStorage.getItem("savedUsername");
        let points = await firebase.getPoints(savedUsername);
        document.getElementById("pointsID").innerHTML = "You have " + points + " points";
        SCORE = 0;
        console.log("reset");
        document.getElementById("resetBtn").style.display = "none";
        SHOW_RESULTS = false;
        makeQuestion();
        // setTimeout(startGame, 1500);
        startGame();
    }

    function decreaseSpeed() {
        SPEED--;
        if (SPEED < 0) {
            SPEED = 0;
        }
        document.getElementById("speedID").innerHTML = "Speed: " + SPEED;
    }

    function increaseSpeed() {
        SPEED++;
        if (SPEED > 20) {
            SPEED = 20;
        }
        document.getElementById("speedID").innerHTML = "Speed: " + SPEED;
    }

    function btnMsg(msg) {
        document.getElementById("formMsgID").innerHTML = msg;
        document.getElementById("formSubmitMsg").innerHTML = msg;
        toggleForm();
    }
    
    function toggleForm() {
        if (document.getElementById("loginFormID").style.display === "block") {
            document.getElementById("loginFormID").style.display = "none";
            document.getElementById("openLoginBtnID").style.display = "block";
            document.getElementById("openSignUpBtnID").style.display = "block";
        } else {
            document.getElementById("loginFormID").style.display = "block";
            document.getElementById("openLoginBtnID").style.display = "none";
            document.getElementById("openSignUpBtnID").style.display = "none";
        }
    }

    async function signUser(event) {
        event.preventDefault();
        // check if loggin in or signing up
        if (username.length >= 4 && username.length <= 50) {
            if (password.length >= 4 && password.length <= 50) {
                if (document.getElementById("formMsgID").innerHTML === "Login") {
                    // await firebase.loginUser()
                    console.log("logging in user");
                    loginUser(username, password);
                } else {
                    // sign up check
                    console.log("creating in user");
                    let result = await firebase.signUpUser(username, password);
                    if (result === false) {
                        alert("A username with that email already exists. Please use a different email");
                    } else {
                        loginUser(username, password);
                    }
                }
                console.log("'" + username + "'");
                console.log("'" + password + "'");
            } else {
                alert("Password must be between 4 and 50 characters long");
                setPassword("");
            }
        } else {
            alert("Username must be between 4 and 50 characters long");
        }
    }

    async function loginUser(username, password) {
        let result = await firebase.loginUser(username, password);
        if (result === true) {
            sessionStorage.setItem("savedUsername", username);
            toggleForm();
            await showUserDetails(username);
        } else {
            setPassword("");
            alert(result);
        }
    }

    function logout() {
        sessionStorage.clear();
        setUsername("");
        setPassword("");
        document.getElementsByClassName("signIn")[0].style.display = "none";
        document.getElementsByClassName("signUp")[0].style.display = "block";
    }

    return ( 
        <div className="Flappy">
            <br />
            <div className="signUp">
                <button id="openLoginBtnID" type="submit" className="btn btn-primary" onClick={() => btnMsg("Login")}>Login</button>
                <button id="openSignUpBtnID" type="submit" className="btn btn-success" onClick={() => btnMsg("Create User")}>Create Account</button>
                <form id="loginFormID" style={{display:"none"}} onSubmit={signUser}>
                    <h4 id="formMsgID">Login</h4>
                    <div className="form-group">
                        <label>Username</label>
                        <input type="text" required value={username} onChange={(e) => setUsername(e.target.value)} className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Username" />
                    </div>
                    <div className="form-group">
                        <label>Password</label>
                        <input type="password" required value={password} onChange={(e) => setPassword(e.target.value)} className="form-control" id="exampleInputPassword1" placeholder="Password" />
                    </div>
                    <button id="formSubmitMsg" type="submit" className="btn btn-primary">Login</button>
                    <button className="btn btn-secondary" onClick={toggleForm}>Hide</button>
                </form>
            </div>
            
            <h4 id="scoreID" style={{display: "inline"}}>Score: 0</h4>
            <br />
            <canvas id="gameScreen"  height="300" width="300"></canvas>
            <br />

            <table>
                <tbody>
                    <tr>
                        <td>
                            <div className="signIn" style={{display:"none"}}>
                                <h4 id="usernameID"> </h4>
                                <p id="pointsID"> </p>
                                <button className="btn btn-secondary" onClick={logout}>Logout</button>
                            </div>
                        </td>
                        <td>
                            <button id="resetBtn" className="btn btn-light" onClick={reset}>Play Again?</button>
                            <p id="testID"></p>
                            <button className="speedBtn btn btn-light" onClick={decreaseSpeed} style={{display: "inline"}}>-</button>
                            <p id="speedID" style={{display: "inline"}}>Speed:</p>
                            <button className="speedBtn btn btn-light" onClick={increaseSpeed} style={{display: "inline"}}>+</button>
                        </td>
                    </tr>
                </tbody>
            </table>

            <img id="spriteID" alt="potter" src={potter} style={{display:"none"}}></img>
            <img id="backgroundSkyID" alt="background" src={backgroundSky} style={{display:"none"}} ></img>
            <img id="backgroundTowersID" alt="background" src={backgroundTowers} style={{display:"none"}} ></img>
            <img id="cloudID" alt="background" src={cloud} style={{display:"none"}} ></img>

            <audio controls id="audioID">
              <source src={roblox} type="audio/mpeg" />
              Your browser does not support the audio tag.
            </audio>

            <audio controls id="audioZeldaID">
              <source src={zelda} type="audio/mpeg" />
              Your browser does not support the audio tag.
            </audio>

        </div>
    );
}

export default Flappy;